<template>
    <div class="">
        <c-data-tables @paginate="send" @refresh="refresh" title="Registro de ventas" :limite="[10,20,50,100]" :total="total" :opt="opts">
            <template v-slot:table>
                <table class="table table-condensed table-sm table-hover">
                    <thead>
                    <tr>
                        <th class="col-xs-1">Codigo</th>
                        <th class="col-md-3">Cliente</th>
                        <th>Importe</th>
                        <th style="width:40px">Dcto</th>
                        <th>SubTotal</th>
                        <th>IVA</th>
                        <th>Total</th>
                        <th class="col-xs-1">Cobr√≥</th>
                        <th>Modo</th>
                        <th>Fecha</th>
                        <th></th>
                    </tr>
                    <tr>
                        <th>
                            <div class="input-group">
                                <input @keyup.enter="send" v-model.number="values.id" type="number" class="form-control input-sm">
                                <span class="input-group-btn">
                                <button type="button" @click="values.id=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                            </div>
                        </th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th>
                            <div class="input-group">
                                <input @keyup.enter="send" v-model="values.cobrado_por" class="form-control input-sm">
                                <span class="input-group-btn">
                                <button @click="values.cobrado_por=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                            </div>
                        </th>
                        <th>
                            <select @change="send" v-model="values.modo" class="form-control input-sm">
                                <option value="null">Todos</option>
                                <option value="normal">Normal</option>
                                <option value="urg">Urgencia</option>
                                <option value="m1">Maquila 1</option>
                                <option value="m2">Maquila 2</option>
                            </select>
                        </th>
                        <th>
                            <div class="input-group">
                                <input @keyup.enter="send" v-model="values.fecha" type="date" class="form-control input-sm">
                                <span class="input-group-btn">
                                <button @click="values.fecha=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                            </div>
                        </th>
                        <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(m,i) in model" :key="m.id">
                        <td>{{m.id}}</td>
                        <td>{{m.cliente.nombre}} ( <b>{{m.cliente.telefono}}</b> )</td>
                        <td>{{m.importe}}</td>
                        <td>{{m.descuento==null?'0':m.descuento}}</td>
                        <td>{{m.sub_total}}</td>
                        <td>{{m.iva}}</td>
                        <td>{{m.total}}</td>
                        <td>{{m.cobrado_por}}</td>
                        <td>
                            <span v-if="m.modo==='normal'" class="label label-success">Normal</span>
                            <span v-else-if="m.modo==='urg'" class="label label-danger">Urgencia</span>
                            <span v-else-if="m.modo==='m1'" class="label label-warning">Maquila 1</span>
                            <span v-else-if="m.modo==='m2'" class="label label-info">Maquila 2</span>
                        </td>
                        <td>{{m.created_at}}</td>
                        <td class="btn-group">
                            <button @click="imprimir(m.uuid)" class="btn btn-primary btn-xs"><span class="fa fa-file-pdf-o"></span></button>
                            <button @click="confirmarDelete(m.id,i)" class="btn btn-danger btn-xs"><span class="fa fa-trash"></span></button>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </template>
        </c-data-tables>
        <modal-delete :model="confirmar" @onDelete="eliminar"/>
    </div>
</template>

<script>
    import api from '@/services'
    import modalDelete from '@/components/el/c-delete'
    import session from '@/lib/session'

    export default {
        name: "ventas",
        components:{modalDelete},
        data(){
            return {
                confirmar:{id:0,index:null},
                showModal:false,
                search:false,
                model:[],
                values: {
                    id:null,
                    client_id:null,
                    cobrado_por:null,
                    modo:null,
                    fecha:null,
                },
                total:0,
                opts:{
                    per_page:10,
                    page:1,
                }
            }
        },
        mounted(){
            this.$emit('title','Registro de ventas');
        },
        methods: {
            eliminar(obj){
                api.ventas.destroy(obj.id).then((r)=>{
                    if (r.data){
                        this.$toast.warning();
                        this.model.splice(obj.index,1);
                    } else {
                        this.$toast.error('No se puedo eliminar el registro');
                    }
                }).catch(()=>{
                    this.$toast.catch();
                });
            },
            confirmarDelete(id,i){
                this.confirmar.id = id;
                this.confirmar.index = i;
                window.$('#modal-delete').modal({
                    backdrop:false,
                    show:true
                });
            },
            imprimir(uuid){
                let token = session.getKey('session_lab').api_token;
                window.open(`${window.base_url}/venta/pdf/${uuid}?api_token=${token}`)
            },
            limpiar(){
                this.values.id = null;
                this.values.client_id = null;
                this.values.cobrado_por = null;
                this.values.fecha = null;
                this.values.modo = null;
            },
            send(){
                api.ventas.search(this.getParams()).then((r)=>{
                    if (r.status ===200){
                        this.model = r.data.data;
                        this.total = r.data.total;
                    }
                }).catch(()=>{});
            },
            refresh(){
                this.limpiar();
                this.send();
            },
            getParams(){
                return {...this.opts,params:this.values};
            }
        }
    }
</script>